import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Typography, 
  Box,
  Paper,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
  Chip,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  InputAdornment,
  List,
  ListItem,
  ListItemText,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Avatar
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import PhoneIcon from '@mui/icons-material/Phone';
import MedicalServicesIcon from '@mui/icons-material/MedicalServices';
import FilterListIcon from '@mui/icons-material/FilterList';
import { Map, MapMarker, CustomOverlayMap } from 'react-kakao-maps-sdk';

//       ( API  )
const clinicData = [
  {
    id: 1,
    name: '',
    address: '   520-5',
    phone: '031-795-7722',
    position: { lat: 37.5405, lng: 127.2147 },
    equipments: [
      { id: 1, name: ' ', condition: '', quantity: 3, description: '   .' },
      { id: 2, name: ' ', condition: '', quantity: 2, description: '   .' }
    ]
  },
  {
    id: 2,
    name: '',
    address: '   730-1',
    phone: '031-793-8275',
    position: { lat: 37.5308, lng: 127.2042 },
    equipments: [
      { id: 3, name: '', condition: '', quantity: 5, description: '   .' },
      { id: 4, name: '', condition: '', quantity: 2, description: ' .' }
    ]
  },
  {
    id: 3,
    name: '',
    address: '   452-2',
    phone: '031-792-8253',
    position: { lat: 37.5467, lng: 127.2063 },
    equipments: [
      { id: 5, name: '  ', condition: '', quantity: 1, description: '6 ,  .' },
      { id: 6, name: ' ', condition: '', quantity: 3, description: ' .' }
    ]
  },
  {
    id: 4,
    name: '',
    address: '   762-5',
    phone: '031-794-6633',
    position: { lat: 37.5387, lng: 127.2114 },
    equipments: [
      { id: 7, name: ' ', condition: '', quantity: 10, description: '   .' },
      { id: 8, name: '', condition: '', quantity: 1, description: '1   .' }
    ]
  },
  {
    id: 5,
    name: '',
    address: '   566-2',
    phone: '031-793-7528',
    position: { lat: 37.5392, lng: 127.2219 },
    equipments: [
      { id: 9, name: '', condition: '', quantity: 4, description: '  .' },
      { id: 10, name: ' ', condition: '', quantity: 2, description: '  .' }
    ]
  }
];

const FreeMedicalEquipmentPage: React.FC = () => {
  const [selectedClinic, setSelectedClinic] = useState<number | null>(null);
  const [equipmentType, setEquipmentType] = useState<string>('');
  const [condition, setCondition] = useState<string>('');
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [openDialog, setOpenDialog] = useState<boolean>(false);
  const [selectedEquipment, setSelectedEquipment] = useState<any>(null);
  
  const equipmentTypes = ['', ' ', '', '', '', ' ', '', ''];
  const conditions = ['', '', '', ''];
  
  const handleMarkerClick = (clinicId: number) => {
    setSelectedClinic(clinicId);
  };
  
  const handleEquipmentClick = (equipment: any) => {
    setSelectedEquipment(equipment);
    setOpenDialog(true);
  };
  
  const handleCloseDialog = () => {
    setOpenDialog(false);
  };
  
  const handleContactClick = (phone: string) => {
    window.location.href = `tel:${phone}`;
  };
  
  //   
  const filteredClinics = clinicData.filter(clinic => {
    //    
    const matchesSearchTerm = 
      searchTerm === '' || 
      clinic.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      clinic.address.toLowerCase().includes(searchTerm.toLowerCase());
    
    //      
    const hasMatchingEquipment = clinic.equipments.some(equipment => {
      const matchesType = equipmentType === '' || equipment.name.includes(equipmentType);
      const matchesCondition = condition === '' || equipment.condition === condition;
      return matchesType && matchesCondition;
    });
    
    return matchesSearchTerm && hasMatchingEquipment;
  });

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 8 }}>
      <Typography variant="h4" gutterBottom>
           
      </Typography>
      <Typography variant="body1" color="textSecondary" paragraph>
               .
      </Typography>
      
      {/*    */}
      <Paper sx={{ p: 2, mb: 3 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              placeholder="   ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid item xs={6} md={4}>
            <FormControl fullWidth>
              <InputLabel> </InputLabel>
              <Select
                value={equipmentType}
                onChange={(e) => setEquipmentType(e.target.value as string)}
                label=" "
                startAdornment={
                  <InputAdornment position="start">
                    <MedicalServicesIcon fontSize="small" />
                  </InputAdornment>
                }
              >
                {equipmentTypes.map((type) => (
                  <MenuItem key={type} value={type}>
                    {type}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={6} md={4}>
            <FormControl fullWidth>
              <InputLabel></InputLabel>
              <Select
                value={condition}
                onChange={(e) => setCondition(e.target.value as string)}
                label=""
                startAdornment={
                  <InputAdornment position="start">
                    <FilterListIcon fontSize="small" />
                  </InputAdornment>
                }
              >
                {conditions.map((cond) => (
                  <MenuItem key={cond} value={cond}>
                    {cond}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
      </Paper>
      
      {/*     */}
      <Grid container spacing={3}>
        {/*  */}
        <Grid item xs={12} md={7}>
          <Paper sx={{ p: 1, height: '500px', position: 'relative' }}>
            <Map
              center={{ lat: 37.5382, lng: 127.2095 }}
              style={{ width: '100%', height: '100%' }}
              level={5}
            >
              {filteredClinics.map((clinic) => (
                <React.Fragment key={clinic.id}>
                  <MapMarker
                    position={clinic.position}
                    onClick={() => handleMarkerClick(clinic.id)}
                    clickable={true}
                  />
                  {selectedClinic === clinic.id && (
                    <CustomOverlayMap
                      position={clinic.position}
                      yAnchor={1.5}
                    >
                      <Box sx={{ 
                        bgcolor: 'background.paper', 
                        p: 1, 
                        borderRadius: 1, 
                        boxShadow: 3,
                        minWidth: '150px',
                        maxWidth: '250px',
                      }}>
                        <Typography variant="subtitle1" fontWeight="bold">
                          {clinic.name}
                        </Typography>
                        <Typography variant="body2">
                           : {clinic.equipments.length}
                        </Typography>
                      </Box>
                    </CustomOverlayMap>
                  )}
                </React.Fragment>
              ))}
            </Map>
          </Paper>
        </Grid>
        
        {/*     */}
        <Grid item xs={12} md={5}>
          <Paper sx={{ p: 2, maxHeight: '500px', overflow: 'auto' }}>
            <Typography variant="h6" gutterBottom>
                ({filteredClinics.length})
            </Typography>
            {filteredClinics.length === 0 ? (
              <Typography variant="body1">  .</Typography>
            ) : (
              filteredClinics.map((clinic) => (
                <Card key={clinic.id} sx={{ mb: 2 }}>
                  <CardContent>
                    <Typography variant="h6" gutterBottom>
                      {clinic.name}
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                      <LocationOnIcon color="action" fontSize="small" sx={{ mr: 0.5 }} />
                      <Typography variant="body2" color="text.secondary">
                        {clinic.address}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                      <PhoneIcon color="action" fontSize="small" sx={{ mr: 0.5 }} />
                      <Typography variant="body2" color="text.secondary">
                        {clinic.phone}
                      </Typography>
                    </Box>
                    <Divider sx={{ my: 1 }} />
                    <Typography variant="subtitle2" gutterBottom>
                        :
                    </Typography>
                    <List dense>
                      {clinic.equipments.map((equipment) => {
                        //     
                        const matchesType = equipmentType === '' || equipment.name.includes(equipmentType);
                        const matchesCondition = condition === '' || equipment.condition === condition;
                        
                        if (matchesType && matchesCondition) {
                          return (
                            <ListItem 
                              key={equipment.id} 
                              button 
                              onClick={() => handleEquipmentClick(equipment)}
                            >
                              <ListItemText 
                                primary={equipment.name} 
                                secondary={`: ${equipment.condition} / : ${equipment.quantity}`}
                              />
                              <Chip 
                                label="" 
                                color="primary" 
                                size="small" 
                                clickable
                              />
                            </ListItem>
                          );
                        }
                        return null;
                      })}
                    </List>
                  </CardContent>
                  <CardActions>
                    <Button 
                      size="small" 
                      color="primary"
                      onClick={() => handleMarkerClick(clinic.id)}
                    >
                       
                    </Button>
                    <Button 
                      size="small" 
                      color="primary"
                      onClick={() => handleContactClick(clinic.phone)}
                    >
                      
                    </Button>
                  </CardActions>
                </Card>
              ))
            )}
          </Paper>
        </Grid>
      </Grid>
      
      {/*     */}
      <Dialog open={openDialog} onClose={handleCloseDialog}>
        {selectedEquipment && (
          <>
            <DialogTitle>
                
            </DialogTitle>
            <DialogContent dividers>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Avatar sx={{ bgcolor: 'primary.main', mr: 2 }}>
                  <MedicalServicesIcon />
                </Avatar>
                <Typography variant="h6">
                  {selectedEquipment.name}
                </Typography>
              </Box>
              <Grid container spacing={2} sx={{ mb: 2 }}>
                <Grid item xs={6}>
                  <Typography variant="body2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {selectedEquipment.condition}
                  </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography variant="body2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {selectedEquipment.quantity}
                  </Typography>
                </Grid>
              </Grid>
              <Typography variant="body2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1" paragraph>
                {selectedEquipment.description}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                 
              </Typography>
              <Typography variant="body1">
                     .     .
              </Typography>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDialog}>
                
              </Button>
              <Button 
                variant="contained" 
                color="primary"
                onClick={() => {
                  const clinic = clinicData.find(c => 
                    c.equipments.some(e => e.id === selectedEquipment.id)
                  );
                  if (clinic) {
                    handleContactClick(clinic.phone);
                  }
                }}
              >
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>
    </Container>
  );
};

export default FreeMedicalEquipmentPage; 